.project-cards-container {
  display: flex;
  flex-wrap: wrap;
  justify-content: center; /* Para centrar las tarjetas horizontalmente */
  gap: 20px;
  padding-top: 15px;
  color: white;
  background-color: #333;
}

.project-card {
  max-width: 400px; /* Establecer un ancho máximo para las tarjetas */
  border: 1px solid #dddddda8;
  border-radius: 8px;
  overflow: hidden;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  margin-bottom: 20px;
  transition: transform 0.3s ease-in-out; /* Add transition effect */
  height: 400px;
}

.project-card:hover {
  transform: scale(1.05); /* Scale up the card on hover */
}

.project-image {
  width: 100%;
  height: 200px;
  object-fit: cover;
}

.project-details {
  padding: 20px;
  text-align: center;
}

.project-details h3 {
  margin-top: 0;
}

.project-details p {
  color: #bebcbc;
}

.project-link {
  text-decoration: none; /* Remove default link styling */
  color: inherit; /* Inherit color from parent */
}

.project-link:hover .project-details {
  background-color: rgba(0, 0, 0, 0.1); /* Change background color on hover */
}

.project-details a {
  display: block;
  margin-top: 10px;
  color: #007bff;
  text-decoration: none;
}

.project-details a:hover {
  text-decoration: underline;
}
